import Vue from 'vue'
import Router from 'vue-router'

import ProductPage from '@/components/pages/ProductPage'
import Cart from '@/components/pages/Cart'
import CheckoutPage from '@/components/pages/CheckoutPage'
import ProductPageAll from '@/components/pages/ProductPageAll'


Vue.use(Router)


export default new Router({
  routes: [
    {
      path: '/',
      name: 'ProductPageAll',
      component: ProductPageAll
    }, 
    {
      path: '/product/:name',
      name: 'ProductPage',
      component: ProductPage
    },
    {
      path: '/cart',
      name: 'cart',
      component: Cart
    },
    {
      path: '/checkoutpage',
      name: 'checkoutpage',
      component: CheckoutPage
    }
  ]
})



