<template>
<div>
  <ProductCard v-bind:products="searchproducts" :key="$route.path"></ProductCard>
</div>
</template>

<script>
import { mapState, mapMutations, mapActions} from 'vuex'
import navbar from "../narbar";
import ProductCard from "../ProductCard";
export default {
  data () {
    return {    
    }
  },
  computed:{
      ...mapState({
        products: state => state.products.all
      }),
      searchproducts(){
          return this.products.filter(product=>product.engtag==this.$route.params.name)
      }

  },
  components:{
    navbar: navbar,
    ProductCard: ProductCard,
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



